import React from "react";
import "./index.css";
import {Link} from "react-router-dom";

const Home = () => (
    <div>
        <header class="header">
            <span class="main-text">Claudia Jäger</span>
        </header>

        <div class="wrapper">
            <section class="projects color">
                <div class="container">
                    <div class="row">
                    {/* Left side */}
                        <div class="col-sm left-content">
                            <figure>
                                <img class="home-image" src="https://i.imgur.com/fNT5hfh.jpg" alt="project"/>
                            </figure>
                        </div>
                        {/* Right side */}
                        <div class="col-sm right-content">
                            <h2 id="home-page-h2">FridgID</h2>
                            <p>Conceptualize a prototype of a mobile experience designed for a smart-fridge.</p>
                            <Link to={"/fridgid"} class="button">Read more</Link>
                        </div>
                    </div>
                </div>
            </section>

            <section class="projects">
                <div class="container">
                    <div class="row">
                        {/* Left side */}
                        <div class="col-sm right-alignment left-content">
                            <h2 id="home-page-h2">Design Projects</h2>
                            <p id="right-alignment">Coming soon.</p>
                            <Link to={"/designprojects"} class="button">Read more</Link>
                        </div>
                        {/* Right side */}
                        <div class="col-sm right-content">
                        <figure>
                            <img class="home-image" src="https://i.imgur.com/ABnGVkV.png" alt="designprojects"/>
                        </figure>
                        </div>
                    </div>
                </div>
            </section>
        </div>

        {/* Bottom of page */}
        <div>
            <section class="more-work text-center">
                <li id="more-work"><Link to={"/work"} id="work-link">View other work 🡢</Link></li>
            </section>

            <section class="projects color display">
                <div class="container">
                    <div class="row">
                    {/* Left side */}
                        <div class="col-2">
                            <figure>
                                <img id="signature" src="https://i.imgur.com/AMKHMOO.png" alt="sign"/>
                            </figure>
                        </div>
                        {/* Right side */}
                        <div class="col-8 background">
                            <h2 id="about-website">About this Website</h2>
                                <p id="about-text">This website has been designed and built from the ground up using <a href="https://reactjs.org/">ReactJS</a>.<br/>
                                <b>Any questions or suggestions? </b><br/> 
                                E-mail me at <a href="mailto:claudia.jager@outlook.com" id="link-email">claudia.jager@outlook.com </a>☺
                                </p>
                        </div>
                    </div>
                </div>
            </section>
            
            <footer class="page-footer font-small">
                <div class="footer-copyright">Copyright © 2020 Claudia Jäger</div>
            </footer>
        </div>

    </div>
    );

    export default Home;