import React from 'react'
import "./index.css";

const About = () => (
    <div>
        <header class="header">
            <span class="main-text">About</span>
        </header>

        <section class="projects">
            <div class="container">
                <div class="row justify-content-center">
                    {/* Left side  */}
                    {/* <div class="col-4"></div> */}
                    <div class="col-6">
                        <h5 id="about-title">Hi, I'm Claudia!</h5>
                        <p id="about-content">I'm a <b>UX design student</b> based in Stockholm, currently learning about <b>front-end development</b> on the side. I strive to be able to gather <b>real-world data</b> from users' experiences to innovate empathetic and inclusive design solutions.</p>
                        <p id="about-content">Prior to tech, I came from <b>Japanese language studies</b> and lived in Tokyo for a year. This made me passionate about understanding the diversity in cultures and working to bridge language gaps for people and how <b>design</b> could participate in making information more accessible in different places around the world.</p>
                        <p id="about-content">Aside from current studies, you can probably find me painting (acrylics), traveling, and chatting to friends over a cup of coffee somewhere in Tokyo or Stockholm. <br/>
                        Feel free to reach out for a chat and a coffee! ☺</p><br/>
                        {/* Right side */}
                    </div>
                    <div class="col-6">
                            <h5 id="jp-title">イェーガー　クラウディア</h5>
                            <p id="jp-about-content">ストックホルムに住むUXデザインの学生で、現在フロントエンドも独学しています。将来的にはUXとフロントエンドの知識を活かして、ユーザーに最適かつインクルーシブなデザインをしたいです。</p>
                            <p id="jp-about-content">ITを専攻する前に日本語学の学士号を取得しました。その中で早稲田大学へ１年間交換留学をしました。これにより文化の多様性や人々の言語のギャップを埋めることに興味を持ち、デザインによりこの課題に取り組みたいと考えました。将来、日本で働きたいと考えています。</p>
                            <p id="jp-about-content">現在の学生生活の他に、私は絵を描いたり、旅行したり、東京やストックホルムのどこかでコーヒーを飲みながら友達とおしゃべりをしています。<br/>
                            お気軽にご連絡下さい！<br/>
                            コーヒーを飲みながらお話しましょう ☺</p>
                    </div>
                </div>
            </div>
        </section>

        <section class="projects">
            <div class="container">
                <div class="row">
                    {/* Left side */}
                    <div class="col-sm-8">
                        <img id="about-image" src="https://i.imgur.com/6WPYXpt.jpg" alt="about"/>
                        <p class="figcaption">Photo by Isomura Takuya</p>
                    </div>
                    {/* Right side */}
                    <div class="col-sm">
                        <p class="about-me"> That's me! </p>
                    </div>
                    
                    
                </div>
            </div>
        </section>

        <section class="projects skills-section">
            <h2 id="skills-title">Skills</h2>
                <div class="container">
                    <div class="row justify-content-center">
                        {/* Left side  */}
                        <div class="col-sm">
                            <p id="skills-content">I'm a trilingual teamwork-loving UX design admirer with a recently found passion for web development (ReactJS) and digital marketing. <br/>Following is a set of skills I feel most confident in (no particular order), if you'd like more information please drop me a message.</p>
                        </div>
                    </div>
                    <div class="row justify-content-center">
                        <div class="col order-first section-config">
                            <h3 class="h3-extra-titles">Skills</h3>
                            <ul class="about-lists">
                                <li>● HTML5</li>
                                <li>● CSS</li>
                                <li>● ReactJS</li>
                                <li>● Bootstrap</li>
                                <li>● Prototyping</li>
                                <li>● UI / UX Design</li>
                                <li>● Usability Testing</li>
                                <li>● User Stories / Personas</li>
                            </ul>
                        </div>
                        <div class="col order-last section-config">
                            <h3 class="h3-extra-titles">Tools</h3>
                            <ul class="about-lists">
                                <li>● Figma</li>
                                <li>● Axure RP</li>
                                <li>● Adobe Fresco</li>
                                <li>● Adobe Illustrator</li>
                                <li>● Adobe Photoshop</li>
                                <li>● Visual Studio Code</li>
                                <li>● Firefox / Edge Dev Tools</li>
                            </ul>
                        </div>
                        <div class="col section-config">
                            <h3 class="h3-extra-titles">Languages</h3>
                                <ul class="about-lists">
                                    <li>● Swedish</li>
                                    <li>● English</li>
                                    <li>● Japanese</li>
                                </ul>
                        </div>
                    </div>
                    {/* <div class="row">
                        <div class="col-2">
                            <figure>
                                    <img id="butterfly" src="https://i.imgur.com/AMKHMOO.png" alt="sign"/>
                            </figure>
                        </div>
                    </div> */}
                    {/* <div class="row center-position section-config">
                        <div class="col">
                            
                        </div>
                    </div> */}
                </div>            
        </section>
        
        <section class="projects color about-color">
            <div class="container">
                <div class="row justify-content-around">
                    {/* Left side */}
                    <div class="col-6">
                        <h2>Get in Touch</h2>
                        <p>I'm currently based in Stockholm with studies in Computer and System Sciences (major: Interaction Design) until 2022 at Stockholm University. I previously graduated from Stockholm University and Waseda University with a B.A in Asian Languages and Cultures.</p><p id="jp-about-content">２０２２年６月卒業予定です。</p>
                        <p>Want to get in touch? Let's get <a href="mailto:claudia.jager@outlook.com" id="link-email">coffee! </a><br/>
                        お気軽に<a href="mailto:claudia.jager@outlook.com" id="link-email jp-about-content">ご連絡ください。</a></p>
                        <p>Resume available upon request.</p>
                            <wrapper class="social-links">
                                <h5><b>Social:</b></h5>
                                    <a href="https://www.linkedin.com/in/claudia-jager/"><img class="social-icon-list" src="data:image/svg+xml;base64," alt="linkedin"/></a>

                                    <a href="https://github.com/claudiajaeger"><img class="social-icon-list" src="data:image/svg+xml;base64," alt="github"/></a>

                                    <a href="https://www.facebook.com/jaeger.claudiamarie"><img class="social-icon-list" src="data:image/svg+xml;base64," alt="facebook"/></a>
                            </wrapper>
                    </div>
                    <div class="col-4">
                        <img id="about-portrait" src="https://i.imgur.com/k0BSwla.png" alt="profile"/>
                    </div>
                </div>
            </div>
        </section>

        <div>

            <footer class="page-footer font-small">
                <div class="footer-copyright text-center py-3">Copyright © 2020 Claudia Jäger</div>
            </footer>
        </div>
    </div>
)

export default About;
