import React from 'react'
import "./index.css"
import {Link} from "react-router-dom";
import Carousel from 'react-bootstrap/Carousel';

const FridgID = () => (
    <div>
        <header class="header">
            <span class="main-text">FridgID</span>
        </header>
        <section class="projects">
            <div class="container">
                <p class="text content-config">Conceptualize a prototype of a mobile experience designed for a smart-fridge.</p>
                <img src="https://i.imgur.com/xIxn59K.jpg" alt="frontimg-fridgid"/>
            </div>
        </section>

        <section class="projects block">
            <div class="container align-row">
                <div class="row">
                    {/* Left side */}
                    <div class="col-sm">
                        <ul>
                            <li><b>Objective:</b><br/>
                            Create an audiovisual prototype from a dynamic system within the theme of "smart-products", in order to study how an intelligent mobile experience could be integrated into a person's daily lifestyle. 
                            </li>
                            <li><b>Type:</b> Group project</li>
                            <li><b>Duration:</b> 3 weeks</li>
                        </ul>
                    </div>
                    {/* Right side */}
                    <div class="col-sm">
                        <ul>
                            <li><b>Team:</b><br/>
                            Claudia Jäger: Prototyping, Personas, Research<br/>
                            <a class="configure-link" href="https://www.behance.net/joellynch">Joel Lynch</a>: Prototyping, Illustration, Design<br/>
                            <a class="configure-link" href="https://www.linkedin.com/in/elias-lundahl-8702561a1/">Elias Lundahl</a>: System Developer
                            </li>
                            <li><b>Tools:</b><br/>
                            Processing<br/>
                            Adobe Illustrator<br/>
                            Adobe Fresco<br/>
                            Adobe Photoshop
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </section>
        <section class="projects">
                    <h2 class="title-text">The Challenge</h2>
                        <img src="https://i.imgur.com/Dc0QbLw.png" alt="challenge" class="challengeimage1"/>
                        <img src="https://i.imgur.com/Wu3SHY4.png" alt="challenge" class="challengeimage2"/>
        </section>
        <section class="projects block">
            <h2 class="title-text">The Solution</h2>
                <img src="https://i.imgur.com/hIQoS7S.png" alt="solution" class="solutionimage1"/>
                <img src="https://i.imgur.com/Y8bfyW2.png" alt="solution" class="solutionimage2"/>
        </section>

        <section class="projects">
            <div class="container">
                <h2 class="title-text">Design Process</h2><br/>
                <h3 class="center-text">Research Methods</h3><br/>
                <p id="padding">We decided on the most suitable methods at that time based on time restrictions and audience. We did several iterations based on feedback from other students and professors. We also conducted general market research and did extensive prototype testing. This course did not cover user participation and so <b>the prototype is based on our user story.</b></p><br/>
                <img src="https://i.imgur.com/nM06XFO.png" alt="researchmethod" class="center researchimage1" width="800" height="300"/><br/>
                <img src="https://i.imgur.com/vXMPAt7.png" class="researchimage2" alt="researchmethod"/>
                <h3 class="center-text">User Persona</h3><br/>
                <p id="padding"><b>In order to pinpoint the core goals and challenges</b> of designing a prototype we incorporated our research into user profile. The user profile is a potential client that is already using a smart-fridge at home.</p><br/>
                <img class="center" src="https://i.imgur.com/JkPpbGt.png" alt="userpersona" width="800" height="400"/><br/>
                <p id="padding">The following persona scenario was based on the user profile in order to design the prototype accordingly:</p>
                <p id="padding" class="citation"><i>"Benedict has been requested by his family to cook a vegan dinner for the family. Benedict goes straight from work to the supermarket and wants to know what raw materials are already at home that he can supplement with what he is going to buy. He decides to also find out the raw materials that have the shortest expiration date. When Benedict opens FridgID on his phone, he receives a pop-up notification that the avocado has expired. He clicks 'Okay!' and decides to use it tonight. When the pop-up message is gone, the list of groceries in the fridge is on the front page. Benedict then sees all the other foods in the fridge. When Benedict has exited the app, he realizes that he forgot to check how much oat milk is left in the fridge and then asks the voice assistant Siri. Siri then announces that there is 1 package of oat milk left in the fridge.<br/><br/>
                Benedict is happy to base today's dinner on the ingredients with a short expiration date and continues to buy food at the supermarket. He feels satisfied that he will use foods that are at home that also has a short expiration date instead of being thrown away."</i></p><br/>
                <h3 class="center-text">Prototyping</h3><br/>
                <div class="row">
                    <div class="col-md">
                    <p id="padding" class="margin-up"><b>We used Processing for the simulation of the prototype and programs from the Creative Cloud collection to create the UI.</b> The course was conducted online due to the Covid-19 pandemic, which made us unable to prototype together in person. This was solved by having online meetings, sharing the process through the "share screen"-tool with each other as well as brainstorming using the 635-method. Furthermore, we applied the MVC-model in Processing in order to easily assist each other when needed.</p>
                    </div>
                    <div class="col-md">
                    <Carousel>
                        <Carousel.Item>
                            <img className="d-block w-100" src="https://i.imgur.com/2MxmS5h.png" alt="First slide" width="500" height="500"/>
                        </Carousel.Item>
                        <Carousel.Item>
                            <img className="d-block w-100" src="https://i.imgur.com/SSY9Tcr.png" alt="Second slide" width="500" height="500"/>
                        </Carousel.Item>
                        <Carousel.Item>
                            <img className="d-block w-100" src="https://i.imgur.com/mIZQwtr.png" alt="Third slide" width="500" height="500"/>
                        </Carousel.Item>
                    </Carousel>
                    </div>
                </div><br/>
                <h3 class="center-text">Takeaways</h3><br/>
                <p id="padding"><b>My biggest takeaway from this project</b> is finding out what works best for each team member. Some people want to do all the nits and grits of the design while others just want to code - and that's okay. I also learned how we could really scope down our project while still being transparent about our vision. In the future, it would be fun to venture into all the different processes of prototyping as well as testing the product on intended users.</p>
                
            </div>
        </section>




        <section>
            <li class="list-style"><Link to={"/prestopay"} id="readnext-link">Read next: Design Projects</Link></li>
        </section>
            

        {/* Bottom of page */}
        <div>
            <footer class="page-footer font-small">
                <div class="footer-copyright text-center py-3">Copyright © 2020 Claudia Jäger</div>
            </footer>
        </div>
    </div>
)

export default FridgID;
