import React from 'react'
import "./index.css";
import {Link} from "react-router-dom";


const Work = () => (
    <div>
        <header class="header">
            <span class="main-text">My Work</span>
        </header>
    <div class="wrapper work-wrapper">
        <div class="container">
            <div class="row">
                <div class="col">
                    <figure class="workpage-projects work-page box">
                        <img src="https://i.imgur.com/fNT5hfh.jpg" alt="fridgid" class="image"/>
                            <div class="overlay">
                                <li><Link to={"/fridgid"} class="link-configure">FridgID</Link></li>
                            </div>    
                    </figure>
                </div>
                <div class="col">
                    <figure class="workpage-projects work-page box">
                        <img src="https://i.imgur.com/ABnGVkV.png" alt="design projects" class="image"/>
                            <div class="overlay">
                                <li><Link to={"/designprojects"} class="link-configure">Design Projects</Link></li>
                            </div>
                    </figure>
                </div>
                <div class="w-100"></div>
                <section class="coming-up w-100">
                    <li class="list-style" id="comingsoon-link">Website overhaul: Coming soon</li>
                </section>
                {/* <div class="col-4">
                    <figure class="projects work-page box">
                        <h3>More coming soon..</h3>
                    </figure>
                </div> */}
            </div>
        </div>
    </div>

        <div>
            <footer class="page-footer font-small">
                <div class="footer-copyright text-center py-3">Copyright © 2020 Claudia Jäger</div>
            </footer>
        </div>
    </div>
)

export default Work;
