import React from 'react'
import "./index.css";
import {Link} from "react-router-dom";

const DesignProjects = () => (
    <div>
        <header class="header">
            <span class="main-text">Design Projects</span>
        </header>
        <div class="row">
            <section class="projects">
            <h5><center>Coming soon</center></h5><br/>
            <li class="list-style"><Link to={"/fridgid"} id="readnext-link">Read next: FridgID</Link></li>
            </section>
        </div>
            
        {/* Bottom of page */}
        <div>
            <footer class="page-footer font-small">
                <div class="footer-copyright text-center py-3">Copyright © 2020 Claudia Jäger</div>
            </footer>
        </div>
    </div>
)

export default DesignProjects;